.SingleTable {
    text-align: left;
    border-collapse: collapse;
    box-shadow: 0 0 30px lightgoldenrodyellow;
}

.SingleTable th, .SingleTable td {
    border: 1px solid green;
    font-size: 0.9em;
    padding: 3px;
}

.SingleTable thead {
    background-color: lightgoldenrodyellow;
}

.SingleTable td {
    font-size: 0.8em;
}

.SingleTable tbody tr:nth-child(even) {
    background-color: lightgray;
}

.SingleTable .lowbase {
    color: red;
}
