header nav ul {
    display: flex;
    justify-content: center;
}

header nav ul li {
    list-style-type: none;
}

header nav ul li a {
    text-decoration: none;
    border: 1px solid lightgreen;
    color: darkgray;
    padding: 5px 10px;
    margin: 0 10px;
    border-radius: 10px;
    box-shadow: -3px 3px 5px darkgreen;
}

header nav ul li a.active {
    background-color: lightgreen;
    color: black;
}
