.MeanScoreForm {
    display: flex;
    flex-direction: column;
    max-width: 700px;
    border: 1px solid green;
    border-radius: 10px;
    padding: 20px;
    margin: 30px 0;
    box-shadow: 0 0 20px lightgreen;
}

.MeanScoreForm .form-group {
    display: flex;
}

.MeanScoreForm select, .MeanScoreForm input, .MeanScoreForm textarea {
    font-family: Merienda, Arial, Helvetica, sans-serif;
    font-size: 1.0em;
    margin: 3px 3px;
}

.MeanScoreForm input {
    width: 200px;
}

.MeanScoreForm textarea {
    width: 200px;
    max-width: 200px;
    min-width: 200px;
    min-height: 30px;
}

.MeanScoreForm button {
    background: radial-gradient(ellipse at center, rgb(56, 194, 38) 0%,#52a5e5 100%);
    color: white;
    font-weight: bold;
    border: 1px solid green;
    border-radius: 20px;
    cursor: pointer;
    padding: 8px 25px;
    outline: none !important;
    text-shadow: 1px 1px 1px black;
    font-size: 1.1em;
    font-family: Merienda;
    width: 170px;
}

.MeanScoreForm button:hover {
    background: radial-gradient(ellipse at center, #52a5e5 0%,rgb(56, 194, 38) 100%);
}

@keyframes border {
    0% {
        border-color: darkorange;
    }
    50% {
        border-color: #c9e432; 
    }
    100% {
        border-color: darkorange;
    }
}
.MeanScoreForm input:focus, .MeanScoreForm textarea:focus, .MeanScoreForm select:focus {
    border-color: #c9e432;
    animation-name: border;
    animation-duration: 2s;
    animation-direction: alternate-reverse;
    animation-iteration-count: infinite;
    outline: none;
}
