.MeanTable {
    text-align: left;
    border-collapse: collapse;
    box-shadow: 0 0 30px lightgoldenrodyellow;
}

.MeanTable th, .MeanTable td {
    border: 1px solid green;
    font-size: 0.9em;
    padding: 3px;
}

.MeanTable thead {
    background-color: lightgoldenrodyellow;
}

.MeanTable td {
    font-size: 0.8em;
}

.MeanTable tbody tr:nth-child(even) {
    background-color: lightgray;
}
